@import url(https://fonts.googleapis.com/css2?family=Knewave&family=Roboto&display=swap);
h1 {
  font-size: 35px;
  font-family: "Knewave", cursive;
  background: -webkit-linear-gradient(135deg, rgb(112, 111, 111), #000000);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0.7;
}

.sidebar {
  height: 100%;
  position: fixed;
  width: 20%;
  background-color: #eceaea9a;
  display: inline-block;
  text-align: left;
}

.title {
  width: 90%;
  text-align: center;
  margin: auto;
}

.sidebarSectionTitle {
  font-family: "Roboto", sans-serif;
  color: #ff2e63;
  margin-left: 10%;
  font-size: 12px;
}

@media only screen and (max-width: 600px) {
  .sidebar {
    width: 30%;
  }

  h1 {
    font-size: 20px;
  }
}

.sidebarButton {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  width: 75%;
  height: 45px;
  color: black;
  opacity: 0.7;
  text-align: left;
  border: none;
  position: relative;
  overflow: hidden;
  margin: 5px 0px 0px 25%;
  background: #eceaea00;
}

.sidebarButton:before {
  height: 100%;
  width: 0%;
  background: linear-gradient(#fc2c45, #f86d82);
  bottom: 0;
  left: 0;
  transition: all 0.3s;
  content: "";
  position: absolute;
  opacity: 0.3;
}

.sidebarButton:hover:before,
.sidebarButtonActive:before {
  width: 100%;
}

.btn-last {
  margin-bottom: 20px;
}

label {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  width: 25px;
  color: #ff2e63;
}

.dropdownList {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.dropDownItem {
  width: 90%;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: black;
  border-radius: 5px;
  opacity: 0.7;
  margin: 0px 0px 0px 20%;
  display: flex;
  justify-content: space-evenly;
}
.popTagTriangle {
  width: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 15px solid black;
  margin: 10px 41% 0px 59%;
  opacity: 0.7;
}

.slider {
  -webkit-appearance: none !important;
  height: 15px;
  background: #eaeaea;
  border-radius: 10px;
}

.sliderSize {
  width: 80%;
  margin: 0px;
}

.sliderSpeed {
  width: 60%;
  margin: 0px;
}

.sliderOutput {
  font-family: "Roboto", sans-serif;
  color: black;
  font-size: 15px;
  opacity: 0.7;
  margin: 5px 0px 0px 58.5%;
}

html {
  height: 100%;
}

body {
  background: linear-gradient(to right, #de6262, #ffb88c);

  /* Other gradient options */
  background: linear-gradient(to right, #bdc3c7, #2c3e50);
  /* fav */
  /* background: linear-gradient(#fc2c45, #ff2e62c0); */
}

button:active,
button:focus,
input:active,
input:focus {
  box-shadow: none;
  outline: 0;
}

.visualiser {
  display: flex;
  align-items: flex-end;

  position: fixed;
  margin-left: 20%;
  width: 78%;
  height: 100%;
  padding: 0 1% 0 1%;
  z-index: -1;
}

@media only screen and (max-width: 600px) {
  .visualiser {
    width: 68%;
    margin-left: 30%;
  }
}

.bar {
  border: 1px solid white;
  display: inline-block;
  width: 0px;
  height: 0px;
  transition: height 0.1s ease-out;
}

.slowTransition {
  transition: height 1s ease-out, background-color 0.5s ease-in-out;
}

.fastTransition {
  transition: height 0.1s ease-out;
}

.colouredIn {
  background: rgba(255, 255, 255, 0.253);
}

body {
  margin: 0;
}

